      <h1 class=\"text-blue\">Let's get in touch</h1>\n                            <p class=\"pt-3\">Our experts can help you find the right solutions. Please provide a bit of information and we’ll be in touch.</p>\n                        </div>\n                        <form class=\"row g-3\">\n                            <div class=\"col-md-6 py-2\">\n                                <label for=\"first_name\" class=\"form-label\">First Name</label>\n                                <input type=\"text\" class=\"form-control p-3\" id=\"first_name\">\n                            </div>\n                            <div class=\"col-md-6 py-2\">\n                                <label for=\"last_name\" class=\"form-label\">Last Name</label>\n                                <input type=\"text\" class=\"form-control p-3\" id=\"last_name\">\n                            </div>\n                            <div class=\"col-12 py-2\">\n                                <label for=\"inputAddress\" class=\"form-label\">Company Name</label>\n                                <input type=\"text\" class=\"form-control p-3\" id=\"inputAddress\" placeholder=\"company name\">\n                            </div>\n                            <div class=\"col-6 py-2\">\n                                <label for=\"email\" class=\"form-label\">Email</label>\n                                <input type=\"email\" class=\"form-control p-3\" id=\"inputAddress2\" placeholder=\"Email Address\">\n                            </div>\n                            <div class=\"col-6 py-2\">\n                                <label for=\"email\" class=\"form-label\">Phone Number</label>\n                                <input type=\"text\" class=\"form-control p-3\" id=\"inputAddress2\" placeholder=\"Phone Number\">\n                            </div>\n                            <div class=\"col-12 py-2\">\n                                <label for=\"inputState\" class=\"form-label\">Industry</label>\n                                <select id=\"inputState\" class=\"form-select p-3\">\n                                    <option selected>Select your industry</option>\n                                    <option>Technology</option>\n                                    <option>Health Care</option>\n                                    <option>Non-Profit</option>\n                                </select>\n                            </div>\n                            <div class=\"col-12 py-2\">\n                                <label for=\"inputState\" class=\"form-label\">Number of employees</label>\n                                <select id=\"inputState\" class=\"form-select p-3\">\n                                    <option selected>Select number of your employees</option>\n                                    <option>Self-employed</option>\n                                    <option>1-5 employees</option>\n                                    <option>1-10 employees</option>\n                                    <option>1-15 employees</option>\n                                </select>\n                            </div>\n                            <div class=\"col-12 py-2\">\n                                <label for=\"inputState\" class=\"form-label\">Estimated annual revenue</label>\n                                <select id=\"inputState\" class=\"form-select p-3\">\n                                    <option selected>Select estimated annual revenue</option>\n                                    <option>$1 - $M</option>\n                                    <option>$1M - $10M</option>\n                                    <option>$10M - $20M</option>\n                                    <option>$30M+</option>\n                                </select>\n                            </div>\n                            <div class=\"col-12 mb-5\">\n                                <button type=\"submit\" class=\"btn btn-secondary-2 px-4 py-2\">SUBMIT</button>\n                            </div>\n                        </form>\n                    </div>\n                </div>\n            </div>\n        </div>\n    </div>\n\n    <div class=\"container-fluid pricing\">\n        <div class=\"container\">\n            <div class=\"row justify-center align-center text-center content-2\">\n                <div class=\" col-xxl-8 col-xl-9 col-lg-8 col-md-10 text-light\">\n                    <h2><b>Pricing</b></h2>\n                    <h2 class=\"fw-normal\">Finance-as-a-Service for Startups and Small Businesses under the US $10mn in\n                        Revenue</h2>\n                </div>\n            </div>\n        </div>\n    </div>\n\n    <div class=\"container-fluid\">\n        <div class=\"btn-price-wrapper position-relative overflow-hidden\">\n            <div class=\"row justify-content-center \">\n                <button v-for=\"btn in planBtn\" :key=\"btn\" type=\"submit\" @click=\"toggleOnTop(btn.id)\"\n                        :class=\"currentId.includes(btn.id) ? 'col-xxl-2 col-5 btn  mt-5 mb-5 btn-secondary-2 shadow': 'col-xxl-2 col-5 btn  mt-5 mb-5 btn-light-blue shadow' \">\n                    {{ btn.plan }}\n                </button>\n            </div>\n            <div class=\"currency position-absolute top-0 end-0\">\n                <div class=\"dropdown\">\n                    <button class=\"btn btn-light dropdown-toggle text-blue \" type=\"button\" data-bs-toggle=\"dropdown\"\n                            aria-expanded=\"false\">\n                        currency\n                    </button>\n                    <ul class=\"dropdown-menu dropdown-menu-dark\">\n                        <li><a class=\"dropdown-item text-secondary\" href=\"#\">USD</a></li>\n                        <li><a class=\"dropdown-item text-secondary\" href=\"#\">EUR</a></li>\n                    </ul>\n                </div>\n            </div>\n        </div>\n    </div>\n\n    <div class=\"container-fluid  py-5\">\n        <div class=\"plans px-4\">\n            <div class=\"row justify-content-between\">\n                <div class=\"col-xxl-3 col-xl-4 col-lg-6 col-md-6 col-sm-12 col-12 d-flex align-items-stretch\" v-for=\"plan in priceLog\" :key=\"plan.type\">\n                    <div class=\"bg-light p-3 py-5 mb-4 price-card shadow d-flex flex-column\">\n                        <div class=\"price-text text-center\">\n                            <h5 class=\"mb-2\">{{ plan.type.toUpperCase() }}</h5>\n                            <template v-if=\"plan.price\">\n                                <h1 v-if=\"currentId == 1\" class=\"d-flex align-items-end justify-content-center\"><span >${{ formatPrice(plan.price) }} </span><span class=\"fs-5 pb-2\">/mo</span></h1>\n                                <h1 v-else class=\"d-flex align-items-end justify-content-center\"><span>${{ formatPrice(plan.price * 12 ) }} </span><span class=\"fs-5 pb-2\">/yr</span></h1>\n                            </template>\n                            <p class=\"pt-2\">{{ plan.slug }}</p>\n                        </div>\n                        <div class=\"py-3 text-center\">\n                            <h4>{{ plan.plus }}</h4>\n                        </div>\n                        <div class=\"plan-goal px-2 \">\n                            <template v-for=\"list in plan.keyBenefit\" key=\"planList\">\n                                <div class=\"d-flex align-content-center\">\n                                    <img src=\"/img/bullet.png\" alt=\"bullet\" class=\"mt-1\" width=\"20\" height=\"20\">\n                                    <p class=\"px-2\"> {{ list }}</p>\n                                </div>\n                            </template>\n                        </div>\n                        <div class=\" align-self-center mt-auto col-10 col-xxl-8 mx-auto pt-3\">\n                            <button class=\"btn btn-secondary-2 col-12\" data-bs-toggle=\"modal\" data-bs-target=\"#staticBackdrop\" type=\"button\">{{ plan.btn }}</button>\n                        </div>\n                    </div>\n                </div>\n            </div>\n        </div>\n    </div>\n\n    <div class=\"container py-5\">\n        <div class=\"col-xxl-10 mx-auto\">\n            <div class=\"call-to-action p-xxl-5 p-3 text-center\">\n                <h4 class=\"fw-normal px-1 fs-xl-5 px-xxl-3\">Connect to know more about our customised services covering Strategic or\n                    Business Finance,\n                    Finance Transformation, Analytics, Automation and Outsourced Finance Processes.\n                </h4>\n                <div class=\" align-self-center mt-auto pt-3 col-10 col-xxl-4 col-xl-4 col-lg-6 mx-auto \">\n                    <button class=\"btn btn-secondary-2 col-12\" data-bs-toggle=\"modal\" data-bs-target=\"#staticBackdrop\" type=\"button\">Know More</button>\n                </div>\n            </div>\n        </div>\n    </div>\n\n</template>\n\n<script>\nexport default {\n    name: \"pricing\",\n    data() {\n        return {\n            priceLog: [\n                {\n                    type: 'STANDARD',\n                    price: 449,\n                    slug: 'Business with Revenue <US$ 1m',\n                    keyBenefit: [\n                        'Accounting',\n                        'Chart of Accounts Review',\n                        'Financial Planning & Budgeting Model -Annual Operating Plan',\n                        'Financial Model Update - Monthly',\n                        'Financial Performance Management - MIS (Monthly) including Variance Analysis Report',\n                        'Saas Metrics Performance MIS Report (Monthly)'\n                    ],\n                    btn: 'Get Started'\n                },\n\n                {\n                    type: 'Growth',\n                    price: 999,\n                    slug: 'Businesses with Revenue US$1-2m',\n                    plus: 'STANDARD+',\n                    keyBenefit: [\n                        'Board Deck and SaaS Metrics Scorecard Report',\n                        'Sales Channel ROI Analysis Model',\n                        'Customer Cohort Analysis Model ',\n                        'Financial Model Update - Monthly',\n                        'Management Valuation Analysis Model',\n                        'Investor Executive Summary Report of your SaaS Business'\n                    ],\n                    btn: 'Get Started'\n                },\n\n                {\n                    type: 'Scale',\n                    price: 2199,\n                    slug: 'Businesses with Revenue US$2-5m',\n                    plus: 'GROWTH+',\n                    keyBenefit: [\n                        'Board Deck and SaaS Metrics Scorecard Report',\n                        'Sales Channel ROI Analysis Model',\n                        'Customer Cohort Analysis Model',\n                        'Management Valuation Analysis Model',\n                        'Investor Executive Summary Report of your SaaS Business'\n                    ],\n                    btn: 'Get Started'\n                },\n\n                {\n                    type: 'Custom',\n                    slug: 'Businesses with Revenue over US$5m',\n                    keyBenefit: [\n                        'Customised and Advisory Services ',\n                        'Scenario Modelling - Bottom-up Analysis based on Base Financial Model',\n                        'Market Sizing Summary Report',\n                        'CFO Insights Summary Report for Management Decision-making',\n                    ],\n                    btn: 'Know More'\n                }\n            ],\n\n            planBtn: [\n                {\n                    id: '1',\n                    plan: 'Monthly',\n                },\n                {\n                    id: '2',\n                    plan: 'Annually',\n\n                },\n            ],\n\n            currentId: '1',\n        }\n    },\n\n\n    methods: {\n        toggleOnTop (id) {\n           this.currentId = id;\n        },\n        formatPrice(value) {\n            let val = (value/1).toFixed(2).replace('.', '.')\n            return val.toString().replace(/\\B(?=(\\d{3})+(?!\\d))/g, \",\")\n        }\n    }\n}\n</script>\n\n<style scoped>\n.btn-secondary-2 {\n    background-color: #174684 !important;\n    color: #fff !important;\n    border-radius: 0;\n    padding: 15px 20px 15px 20px !important;\n    text-transform: uppercase;\n}\n.btn-secondary-2:hover {\n    background-color: #316ebe !important;\n    color: #fff !important;\n    border-radius: 0;\n    padding: 15px 20px 15px 20px !important;\n    text-transform: uppercase;\n}\n\n.price-card {\n    transition: outline 0.2s linear;\n    border-radius: 10px;\n    outline: solid 0px #ffffff;\n    cursor: pointer;\n}\n\n.price-card:hover {\n    outline: solid 1px #174684;\n}\n\n.call-to-action {\n    background-color: #C6D8EF;\n}\n\n.btn-light-blue {\n    background-color: #C6D8EF !important;\n    color: #174684 !important;\n    border-radius: 0;\n    padding: 15px 20px 15px 20px !important;\n    text-transform: uppercase;\n}\n\ninput:focus, button:focus, select:focus {\n    box-shadow: none;\n\n}\n</style>\n\n"],"sourceRoot":""}]);
